.FeedbackText {
  color: red;
  font-size: 2rem;
}

.FeedbackScoreboard {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: white;
  border-radius: 15px;
  box-shadow: 1px 4px 13px 2px rgba(0, 0, 0, 0.2);
  height: 300px;
  margin: auto;
  width: 400px;
}

.FeedbackMain {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin-top: 100px;
}

.FeedbackBtns {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
  width: 400px;
}

.FeedbackBtn {
  background-color: #2fc18c;
  color: white;
  font-size: large;
  font-style:normal;
  border-radius: 10px;
  height: 40px;
  width: 195px;
}
